<template>
  <div class="application">
    <application-header/>
    <application-body/>
    <application-footer/>
  </div>
</template>

<script>
  import ApplicationBody from './_components/ApplicationBody'
  import ApplicationFooter from './_components/ApplicationFooter'
  import ApplicationHeader from './_components/ApplicationHeader'

  export default {
    name: 'application',
    components: {
      ApplicationBody,
      ApplicationFooter,
      ApplicationHeader
    }
  }
</script>



