import Vue from 'vue'
/*  Import the root store  */
import store from './store'
/*  Import the router  */
import router from './router'
/*  Import the root container */
import ApplicationContainer from './ApplicationContainer.vue'

/* eslint-disable no-new */
new Vue({
  el: '#application-container',
  store,
  router,
  template: '<ApplicationContainer />',
  components: {ApplicationContainer}
})



