<template>
  <div class="homepage-container text-center">
    <h1>Welcome to the new MOLGENIS UI</h1>
    <div v-html="homePageContent"></div>
  </div>
</template>

<script>
  export default {
    name: 'home-page',
    computed: {
      homePageContent () {
        return this.$store.state.$_settings ? this.$store.state.$_settings.homePageContent : ''
      }
    }
  }
</script>



