<template>
  <div class="row">
    <div class="col-md-3 setting-index">
      <div class="setting-group">
        <div class="setting-group-items">
          <div class="list-group mb-4">
            <li class="list-group-item disabled">Personal settings</li>
            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/account">
              Account
            </router-link>
            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/application">
              Application settings
            </router-link>
          </div>

          <div class="list-group mb-4">
            <li class="list-group-item disabled">Management settings</li>
            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/jobs">
              Job settings
            </router-link>

            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/users">
              Users
            </router-link>

            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/groups">
              Groups
            </router-link>

            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/permissions">
              Permissions
            </router-link>
          </div>

          <div class="list-group mb-4">
            <li class="list-group-item disabled">Plugin settings</li>
            <router-link
              class="list-group-item list-group-item-action"
              to="/settings/plugins">
              Plugins
            </router-link>
          </div>
        </div>
      </div>
    </div>

    <div class="col-md-9">
      <router-view/>
    </div>

  </div>
</template>

<style scoped>
  .setting-index {
    border-right: solid 1px black;
  }

  .setting-group {
    margin-bottom: 1rem
  }

  .router-link-active {
    font-weight: bold;
    border-left: 0.25rem darkred solid;
  }
</style>

<script>
  import store from './_store'

  export default {
    name: 'settings',
    created () {
      /* register module store */
      this.$store.registerModule('$_settings', store)
    }
  }
</script>



