<template>
    <header class="application-header">
        <!-- settings, account, sign-in / sign-out -->
        <nav class="navbar navbar-expand navbar-light">
            <div class="container">
                <div class="navbar-collapse collapse">
                    <ul class="nav navbar-nav ml-auto w-100 justify-content-end">
                        <router-link v-if="settings_access" class="nav-link" to="/settings/account">Settings
                        </router-link>
                        <router-link v-if="!isAuthenticated" class="nav-link" to="/auth/login">Sign in</router-link>
                        <router-link v-else-if="isAuthenticated" class="nav-link" to="/auth/logout">Sign out</router-link>
                    </ul>
                </div>
            </div>
        </nav>

        <!-- core + dynamic apps -->
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
            <div class="container">
                <form class="form-inline">
                    <router-link to="/" class="navbar-brand">
                        <img src="https://avatars1.githubusercontent.com/u/1688158?v=4&s=280" class="align-middle"
                             width="42"
                             height="42"/>
                    </router-link>
                    <input class="form-control molgenis-wide-search-input"
                           type="search" placeholder="Search MOLGENIS" aria-label="Search MOLGENIS">
                </form>

                <button class="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#application-header-content">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="navbar-collapse collapse" id="application-header-content">
                    <ul class="nav navbar-nav ml-3 w-80 center">
                        <router-link v-for="route in routes" v-if="!topRoutes.includes(route.meta.id)" class="nav-link"
                                     :to="route.path" :key="route.name">
                            {{ route.name }}
                        </router-link>
                    </ul>
                </div>
            </div>
        </nav>
    </header>
</template>

<style scoped>
    .molgenis-wide-search-input {
        background-color: #3f4448;
        color: #5c6063;
        border: none;
        font-size: 13px;
        font-weight: bold;
    }

    .application-header {
        margin-bottom: 1rem;
    }

    .nav-link {
        font-size: 14px !important;
        font-weight: 500;
    }
</style>

<script>
  export default {
    name: 'application-header',
    data () {
      return {
        topRoutes: ['home', 'settings', 'authentication', 'account'],
        routes: this.$router.options.routes
      }
    },
    computed: {
      settings_access: function () {
        return this.$store.getters.settings_access
      },
      isAuthenticated: function () {
        return this.$store.getters['$_auth/isAuthenticated']
      }
    }
  }
</script>


