import { ROLES } from '../_services/role.service'

const state = {
  user: {
    authenticated: false,
    role: '',
    name: ''
  }
}

export default {
  state: state,
  mutations: {
    'clear_user': (state) => {
      state.user.authenticated = false
      state.user.role = ''
      state.user.name = ''
    },
    'set_authenticated_user': (state, username) => {
      state.user.authenticated = true
      state.user.role = ROLES.SUPER_ADMIN
      state.user.name = username
    }
  },
  getters: {
    'isAuthenticated': state => state.user.authenticated
  },
  actions: {
    'login': ({commit}, authPackage) => {
      commit('set_authenticated_user', authPackage.username)
    },
    'logout': ({commit}) => {
      commit('clear_user')
    }
  },
  strict: true,
  namespaced: true
}



