<template>
  <div class="row">
    <div class="col-12">
      <table class="table">
        <thead>
        <td>Plugin</td>
        <td>active</td>
        <td>Settings</td>
        </thead>
        <tbody>
        <tr v-for="plugin in plugins">
          <td>{{ plugin.label }}</td>
          <td>
            <input type="checkbox" :checked="plugin.active" @change="activatePlugin(plugin.id)"/>
          </td>
          <td><i class="fa fa-cog"></i></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'plugin-settings',
    methods: {
      activatePlugin (id) {
        this.$store.dispatch('$_settings/activate_plugin', id)
      }
    },
    computed: {
      plugins: function () {
        return this.$store.state.$_settings.plugins
      }
    },
    created () {
      this.$store.dispatch('$_settings/retrieve_available_plugins')
    }
  }
</script>



