import Vue from 'vue'
import Router from 'vue-router'
/*  Top menu items  */
import Settings from '../modules/settings/Settings'
import AuthenticationPage from '../modules/auth/AuthenticationPage'
/*  Core apps + dynamic apps  */
import HomePage from '../modules/application/_components/HomePage'
import DynamicApp from '../modules/dynamicapp/DynamicApp'
/*  Setting sub-routes  */
import AccountSettings from '../modules/settings/_components/AccountSettings'
import ApplicationSettings from '../modules/settings/_components/ApplicationSettings'
import JobSettings from '../modules/settings/_components/JobSettings'
import GroupSettings from '../modules/settings/_components/GroupSettings'
import UserSettings from '../modules/settings/_components/UserSettings'
import PermissionSettings from '../modules/settings/_components/PermissionSettings'
import PluginSettings from '../modules/settings/_components/PluginSettings'

const addDynamicRoutes = (routes) => {
  const pluginRoutes = window.MOLGENIS_PLUGIN_ROUTES || []
  return pluginRoutes.reduce((accumulator, route) => {
    accumulator.push({
      path: '/' + route.id,
      name: route.name,
      component: DynamicApp,
      props: route.props,
      meta: {
        id: route.id
      }
    })
    return accumulator
  }, routes)
}

const getRoutes = () => {
  const routes = [
    {
      path: '/',
      name: 'Home',
      component: HomePage,
      meta: {
        id: 'home'
      }
    },
    {
      path: '/auth/:action',
      name: 'Authentication',
      component: AuthenticationPage,
      props: true,
      meta: {
        id: 'authentication'
      }
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings,
      meta: {
        id: 'settings'
      },
      children: [
        {path: 'account', component: AccountSettings},
        {path: 'application', component: ApplicationSettings},
        {path: 'jobs', component: JobSettings},
        {path: 'groups', component: GroupSettings},
        {path: 'permissions', component: PermissionSettings},
        {path: 'plugins', component: PluginSettings},
        {path: 'users', component: UserSettings}
      ]
    }
  ]
  return addDynamicRoutes(routes)
}

Vue.use(Router)
export default new Router({
  // mode: 'history', FIXME Use history but catch 404 on server for nested routes
  routes: getRoutes()
})



