<template>
    <div class="row">
        <div class="col-12 text-center">
            <h1>Welcome {{ username }}</h1>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'signed-in-page',
    computed: {
      username: function () {
        return this.$store.state.$_auth.user.name
      }
    }
  }
</script>


