<template>
  <div class="row justify-content-center text-center">
    <div class="col-auto">
      <h1>{{ message }}</h1>
      <router-link v-if="loggedOut" to="/">Return to the homepage</router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'sign-out-page',
    data () {
      return {
        message: 'Signing out...',
        loggedOut: false
      }
    },
    created () {
      this.$store.dispatch('$_auth/logout').then(() => {
        this.message = 'You have been signed out'
        this.loggedOut = true
      })
    }
  }
</script>



