<template>
  <div class="row">
    <div class="col">
      <sign-in-page v-if="action === 'login' && !isAuthenticated"/>
      <signed-in-page v-else-if="action === 'login' && isAuthenticated"/>
      <signed-out-page v-else-if="action === 'logout'"/>
    </div>
  </div>
</template>

<script>
  import SignInPage from './_components/SignInPage'
  import SignedInPage from './_components/SignedInPage'
  import SignedOutPage from './_components/SignedOutPage'

  export default {
    name: 'authentication-page',
    props: {

      /*
     * What action to take (login / logout)
     */
      action: {
        type: String,
        required: true
      },

      /*
       * redirect URL which will be called if
       * authentication is successful
       */
      redirect: {
        type: String,
        required: false,
        default: '/login/success'
      }
    },
    computed: {
      isAuthenticated: function () {
        return this.$store.getters['$_auth/isAuthenticated']
      }
    },
    components: {
      SignInPage,
      SignedInPage,
      SignedOutPage
    }
  }
</script>



