import api from '@molgenis/molgenis-api-client'

export default {
  state: {
    plugins: []
  },
  getters: {},
  mutations: {
    'set_plugins': (state, plugins) => {
      state.plugins = plugins
    }
  },
  actions: {
    'retrieve_available_plugins': ({commit}) => {
      api.get('/plugins/list').then(response => {
        commit('set_plugins', response)
      }, error => {
        console.log(error)
      })
    },
    'activate_plugin': ({commit, state}, id) => {
      api.post('/plugins/activate?id=' + id).then(response => {
        commit('set_plugins', state.plugins.map(plugin => {
          if (plugin.id === id) plugin.active = true
          return plugin
        }))
      })
    },
    namespaced: true,
    strict: true
  }
}



