import Vue from 'vue'
import Vuex from 'vuex'

// import store modules
import auth from '../modules/auth/_store'

import { ROLES } from '../modules/auth/_services/role.service.js'

const state = {}

Vue.use(Vuex)
export default new Vuex.Store({
  state: state,
  modules: {
    '$_auth': auth
  },
  getters: {
    'settings_access': (state) => {
      return state.$_auth.user.authenticated && state.$_auth.user.role === ROLES.SUPER_ADMIN
    }
  },
  mutations: {},
  actions: {},
  strict: true
})



